export default {
	name:"refresh",
	props:{
		widthImg:{
			type:String,
			default:"40rpx"
		},
		heightRh:{
			type:String,
			default:"70rpx"
		},
		msgShow:{
			type:Boolean,
			default:true
		},
		image:{
			type:String,
			default:"../../static/lt1.png"
		}
			
	},	
	created() {
	   uni.$on('reMsg',(data)=>{
			if(data==-1){
				this.propValue=!this.propValue
			}else{
				this.scrollTop=data
			}
		})
	},
	data() {
		return {
			refreshText:"下拉刷新",
			scrollTop:0,
			propValue:true,
		}
	},
	methods: {
		refresh(i){
			if(i==0){
				this.refreshText="松开刷新"
			}else if(i==1){
				// 这里写刷新业务
				this.refreshText="正在刷新"
				this.$emit('down',1)
			}else if(i==2){
				this.refreshText="下拉刷新"
			}
		}
	}
}
